<template>
  <div>
    <base-header class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">
            TuneKey Subscription
          </h6>
        </div>
        <div class="col-lg-6 col-5 text-right">
          <!-- <base-button size="sm" type="neutral">Add new teammate</base-button> -->
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt-6">
      <button-with-loading
        text="TEST BUTTON AND TOAST: success"
        @start="start(1)"
        @end="success"
        width="320"
        :complete="result1"
      ></button-with-loading>
      <br />
      <button-with-loading
        text="TEST BUTTON AND TOAST: error"
        @start="start(2)"
        @end="error"
        width="320"
        :complete="result2"
      ></button-with-loading>
      <br />
      <button-with-loading
        text="TEST BUTTON AND TOAST: warning"
        @start="start(3)"
        @end="warning"
        width="320"
        :complete="result3"
      ></button-with-loading>
      <br />
      <button-with-loading
        text="TEST BUTTON AND TOAST: info"
        @start="start(4)"
        @end="info"
        width="320"
        :complete="result4"
      ></button-with-loading>
    </div>
  </div>
</template>

<script>
import buttonWithLoading from "../components/layout/ButtonWithLoading";
export default {
  components: {
    buttonWithLoading,
  },
  data() {
    return {
      result1: false,
      result2: false,
      result3: false,
      result4: false,
      verticalAlign: 'top',
      horizontalAlign: 'right'
    };
  },
  methods: {
    start(num) {
        let self = this
        setTimeout(() => {
            switch(num) {
                case 1:
                    self.result1 = true
                    break
                case 2:
                    self.result2 = true
                    break
                case 3:
                    self.result3 = true
                    break
                case 4:
                    self.result4 = true
                    break
            }
        }, 2000)
    },
    success() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: 'Success',
        type: "success",
        timeout: 2000,
      });
      this.result1 = false
    },
    error() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: 'Error',
        type: "error",
        timeout: 2000,
      });
      this.result2 = false
    },
    warning() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: 'Warning',
        type: "warning",
        timeout: 2000,
      });
      this.result3 = false
    },
    info() {
      this.$notify({
        verticalAlign: this.verticalAlign,
        horizontalAlign: this.horizontalAlign,
        message: 'Info',
        type: "info",
        timeout: 2000,
      });
      this.result4 = false
    },
  },
};
</script>
